import moment from 'moment'

export interface Coords {
  lat: number
  lon: number
}

export class Location {
  address: string
  geo?: Coords // optional when creating event

  constructor (data: { address: string, geo?: Coords }) {
    this.address = data.address
    this.geo = data.geo
  }
}

export enum Weekday {
  mon = 'mon',
  tue = 'tue',
  wed = 'wed',
  thu = 'thu',
  fri = 'fri',
  sat = 'sat',
  sun = 'sun',
}

export class Era {
  type: {
    until: Date | null
    instances: number | null
    infinite: boolean | null
  }
  interval: number | null
  rrule: {
      daily: boolean | null
      weekly: Weekday[]
      monthly: {
        on: number | null
        weekday: Weekday[]
      }
      yearly: boolean | null
  }

  constructor(data?: any) {
    this.type = data?.type || { until: null, instances: null, infinite: null }
    this.interval = data?.interval || null
    this.rrule = data?.rrule || {
      daily: null,
      weekly: [],
      monthly: {
        on: null,
        weekday: []
      },
      yearly: null
    }
  }
}

export class Period {
  period: {
    start: Date, // in milliseconds since epoch
    end: Date // in milliseconds since epoch
  }

  constructor(data?: any) {
    this.period = {
      start: data?.period?.start ? new Date(data.period.start) : new Date(),
      end: data?.period?.end ? new Date(data.period.end) : new Date(),
    }
  }

  setStart = (start: Date) : Period => {
    this.period = { start, end: this.period.end }
    return this
  }

  setEnd = (end: Date) : Period => {
    this.period = { end, start: this.period.start }
    return this
  }
}

export class EventDetail {
  title: string
  desc: string
  location?: Location

  constructor (data: { title: string, desc: string, loc: any }) {
    this.title = data.title
    this.desc = data.desc
    this.location = data.loc ? new Location(data.loc) : undefined
  }
}

export interface EventAbout {
  organizer: string
  created: Date
  modified: Date
}

export class EventInvite {
  note: string
  optional: boolean
  rsvp: Rsvp
  sentAt: Date

  constructor (data: any) {
    this.note = data.note
    this.optional = data.optional
    this.rsvp = data.rsvp
    this.sentAt = new Date(data.sentAt)
  }
}

export enum Rsvp {
  yes = 'yes',
  no = 'no',
  maybe = 'maybe',
}

export class EventForm {
  calendarCode: string | undefined
  eventCode: string | undefined
  organizer: string
  title: string
  desc: string | undefined
  tzid = 'utc'
  location: string | undefined
  start: Date
  end: Date
  era: Era | undefined

  constructor(data: any) {
    this.calendarCode = data.calendarCode
    this.eventCode = data.eventCode
    this.organizer = data.organizer
    this.title = data.title
    this.desc = data.desc
    this.location = data.location
    this.start = data.start
    this.end = data.end
    this.era = data.era
  }

  toExportFormat = () => ({
    'calendar-code': this.calendarCode,
    'event-code': this.eventCode,
    organizer: this.organizer,
    title: this.title,
    desc: this.desc,
    tzid: this.tzid,
    location: this.location,
    when: { period: { start: this.start.getTime(), end: this.end.getTime() } },
    era: this.era
  })
}

export default class Event {
  eventCode: string
  calendarCode: string
  about: EventAbout
  detail: EventDetail
  when: Period
  era: Era
  invites: EventInvite[]
  rsvp: Rsvp

  constructor(data: any) {
    this.eventCode = data['event-code']
    this.calendarCode = data['calendar-code']
    this.about = data.about
    this.detail = new EventDetail(data)
    this.when = new Period(data.when)
    this.era = new Era(data.era)
    this.invites = (data.invites || []).map((invite) => new EventInvite(invite))
    this.rsvp = data.rsvp || Rsvp.yes
  }
}
