import React, { Component } from 'react'
import moment from 'moment'

import { BaseInput, Box, Text } from '@tlon/indigo-react'
import { Timeframe } from '../../types/Calendar'
import { getFirstOfWeek, getLastOfWeek, spansMonths } from '../../lib/dates'

interface Props {
  selectedDate: Date
  selectDate: (event: React.ChangeEvent<HTMLInputElement>) => void
}

export default class DatePicker extends Component<Props> {
  constructor(props) {
    super(props)
  }

  render() {
    const { props: { selectedDate, selectDate } }= this
      
    return <Box margin='20px 0px 0px' padding="8px" borderRadius="4px" backgroundColor="#f1f3f4">
      <BaseInput type="date" fontSize='14px' fontWeight="100"
        border='none' backgroundColor="#f1f3f4" zIndex={-1}
        value={moment(selectedDate).format('YYYY-MM-DD')}
        onChange={selectDate} />
    </Box>
  }
}
