import React, { Component } from 'react';
import _, { capitalize } from 'lodash';

import { Text, Box, Button, StatelessTextInput, StatelessTextArea } from '@tlon/indigo-react';
import moment from 'moment';
import Calendar, { NavDirection, Timeframe } from '../types/Calendar';
import { match, RouteComponentProps, withRouter } from 'react-router-dom';
import { Location, History } from 'history'
import Event, { Era, EventForm, Period } from '../types/Event';
import DatePicker from '../components/lib/DatePicker';
import Actions from '../logic/actions';

enum EventField {
  title = 'title',
  location = 'location'
}

interface RouterProps {
  calendar: string
  event: string
}

interface Props extends RouteComponentProps<RouterProps> {
  history: History
  location: Location
  match: match<RouterProps>
  calendars: Calendar[]
  actions: Actions
  ship: string
}

interface State {
  calendarCode?: string
  eventCode?: string
  organizer: string
  title: string
  desc: string
  location: string
  start?: Date
  end?: Date
  era: number
  //TODO: add all event props here
  event: Event
}

class EventView extends Component<Props, State> {
  constructor(props) {
    super(props)

    const { calendar, event } = props.match.params

    const eventToEdit = (props.calendars.find(({ calendar_code }) => calendar_code == calendar) || { events: [] }).events.find(({ event_code }) => event_code == event)

    this.state = {
      calendarCode: props.calendars[0]?.calendarCode,
      organizer: props.ship,
      title: '',
      desc: '',
      location: '',
      start: new Date(),
      end: moment().add(30, 'minutes').toDate(),
      era: new Era(),
      event: eventToEdit,
      ...(eventToEdit || {})
    }
  }

  static getDerivedStateFromProps = (props: Props, state: State) => {
    return { ...state, calendarCode: props.calendars[0]?.calendarCode }
  }

  saveEvent = () => {
    const { state, props } = this
    const eventToSave = new EventForm(state)
    if (state.event) {

    } else {
      props.actions.createEvent(eventToSave)
    }

    props.history.goBack()
  }

  deleteEvent = () => {

  }

  updateValue = (field: EventField) => (e: React.ChangeEvent<HTMLInputElement>) : void => {
    const values = {}
    values[field] = e.target.value
    this.setState(values)
  }

  updateDescription = (e: React.ChangeEvent<HTMLTextAreaElement>) : void => {
    this.setState({ desc: e.target.value })
  }

  selectDate = (isStart: boolean) => (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = isStart ? { start: new Date(e.target.value) } : { end: new Date(e.target.value) }
    this.setState(value)
  }

  render() {
    const { state: { title, desc, location, start, end, era, event },
      props: { history },
      saveEvent, deleteEvent, updateValue, selectDate, updateDescription } = this

    const saveDisabled = !title

    return <Box height='100%' p='4' display='flex' flexDirection='column' borderWidth={['none', '1px']} borderStyle="solid" borderColor="washedGray">
      <Box width='100%' display='flex' flexDirection='row'>
        <Button fontSize='16px' marginRight='20px' onClick={history.goBack}>X</Button>
        <StatelessTextInput fontSize="1" placeholder="Event title" width='40%' marginRight='20px' onChange={updateValue(EventField.title)} value={title} />
        <Button disabled={saveDisabled} className='dark' marginRight='20px' onClick={saveEvent}>Save</Button>
        {!!(event?.about) && <Button onClick={deleteEvent}>Delete</Button>}
      </Box>
      <Box width='100%' display='flex' flexDirection='row'>
        <DatePicker selectedDate={start} selectDate={selectDate(true)} />
        {/* <TimePicker selectedDate={when} selectDate={selectDate} /> */}
        <Text fontSize="1" margin="28px 12px 0px">to</Text>
        <DatePicker selectedDate={end} selectDate={selectDate(false)} />
        {/* <TimePicker selectedDate={when} selectDate={selectDate} /> */}
      </Box>
      <StatelessTextInput fontSize="14px" placeholder="Add location" width='60%' margin="20px 0px 0px" onChange={updateValue(EventField.location)} value={location} />
      <StatelessTextArea fontSize="14px" margin="20px 0px 0px" height="160px" placeholder="Add description" width='60%' onChange={updateDescription} value={desc} />
    </Box>
  }
}

export default withRouter(EventView)
